import React from 'react';

import styles from './AboutShop.module.scss';

import classNames from 'classnames/bind';

const cx = classNames.bind(styles);

function AboutShop() {
    return (
        <>
            <div className={cx('wrapper', 'distance-56')}>
                <div className={cx('grid__row', 'distance-32')}>
                    <div className={cx('description')}>
                        <div
                            className={cx('description-img', 'grid__column-2', 'height-img-2-3')}
                            style={{
                                backgroundImage:
                                    "url('https://firebasestorage.googleapis.com/v0/b/ban-hang-a8c40.appspot.com/o/img%2Fswe-buom-mymo%2F1-8.jpeg?alt=media&token=1fe3954e-4ed6-4a7c-b637-da9e8499ca76')",
                            }}
                        ></div>
                        <div className={cx('description-content', 'grid__column-2-3')}>
                            <h3 className={cx('description-title')}>NTSTORE.VN - Ca hng thi trang gi tt</h3>
                            <div className={cx('description-note')}>
                                <p>Sn phm a dng p ng mi nhu cu ca bn</p>
                                <p>
                                    Khuyn mi trn tng sn phm mong mun mang n cht lng tt nht cho khch hng.
                                </p>
                                <p>Giao hng nhanh -  size -  mu - Nhiu mu mi</p>
                            </div>
                            <div className={cx('description-more')}>
                                <button className={cx('btn-circle')}>TM HIU THM..</button>
                            </div>
                        </div>
                    </div>
                    <div className={cx('services')}>
                        <h5 className={cx('services-label')}>Min Ph Ship t 200k</h5>
                        <h5 className={cx('services-label')}>H Tr Chm Sc Khch Hng</h5>
                        <h5 className={cx('services-label')}>14 ngy i tr sn phm li</h5>
                    </div>
                </div>
            </div>
        </>
    );
}

export default AboutShop;
